import http from 'src/services/network';
import { storePageConfig } from 'src/services/pages/actions';
import { recaptchaKeySelector } from 'src/services/options/selectors';
import LocalStorage from 'src/utils/LocalStorage';


const getPageConfig = (pageName) => {
  const pageKey = `${LocalStorage.keys.PAGE_CONFIG}_${pageName}`;
  const storedConfig = LocalStorage.getItem(pageKey);
  if (storedConfig) return Promise.resolve(storedConfig);

  const url = `${SERVER_URL}/pages/?name=${pageName}`;

  return http.get(url)
    .then((res) => {
      LocalStorage.storeItem(pageKey, res.data);
      return res.data;
    });
};

const getFormConfig = (formId) => {
  const url = `${SERVER_URL}/forms?_id=${formId}`;

  return http.get(url)
    .then(res => res.data);
};

export const getAndStorePageConfig = pageName => dispatch => getPageConfig(pageName)
  .then((data) => {
    const { form } = data[0];

    // If a page with form - fetch form data,
    // because the page config doesn't contain form attributes
    if (form) {
      return getFormConfig(form._id)
        .then((formData) => {
          // eslint-disable-next-line no-param-reassign
          data[0].form = formData[0];
          return data;
        });
    }
    return data;
  })
  .then(data => dispatch(storePageConfig(pageName, data[0])));


export const loadRecaptcha = () => (dispatch, getState) => {
  if (document.getElementById('recaptcha-api')) return;

  const key = recaptchaKeySelector(getState());
  const onLoad = () => {
    grecaptcha.ready(() => {
    });
  };

  const script = document.createElement('script');

  script.onload = onLoad;
  script.async = true;
  script.defer = true;
  script.id = 'recaptcha-api';
  script.src = `https://www.google.com/recaptcha/api.js?render=${key}`;

  document.body.appendChild(script);
};
