import { storeOptions } from 'src/services/options/actions';
import http from 'src/services/network';
import { filterServices } from 'src/services/options/utils';
import LocalStorage from 'src/utils/LocalStorage';


const getOptions = () => {
  const storedOptions = LocalStorage.getItem(LocalStorage.keys.OPTIONS);
  if (storedOptions) return Promise.resolve(storedOptions);

  const url = `${SERVER_URL}/options`;

  return http.get(url)
    .then((res) => {
      LocalStorage.storeItem(LocalStorage.keys.OPTIONS, res.data);
      return res.data;
    });
};

export const getAndStoreOptions = () => dispatch => getOptions()
  .then((data) => {
    filterServices(data);
    dispatch(storeOptions(data));
  });
