import MarkerClusterer from '@google/markerclusterer';
import http from 'src/services/network';
import kioskIcon from 'public/icons/map/kiosk.svg';
import terminalIcon from 'public/icons/map/terminal.svg';
import officeIcon from 'public/icons/map/office.svg';
import LocalStorage from 'src/utils/LocalStorage';


const getKiosks = () => {
  const storedKiosks = LocalStorage.getItem(LocalStorage.keys.KIOSKS);
  if (storedKiosks) return Promise.resolve(storedKiosks);

  const url = `${API_URL}/kiosks/`;

  return http.get(url)
    .then((res) => {
      LocalStorage.storeItem(LocalStorage.keys.KIOSKS, res.data);
      return res.data;
    });
};

const getRetailers = () => {
  const storedRetailers = LocalStorage.getItem(LocalStorage.keys.RETAILERS);
  if (storedRetailers) return Promise.resolve(storedRetailers);

  const url = `${API_URL}/retailers/`;

  return http.get(url)
    .then((res) => {
      LocalStorage.storeItem(LocalStorage.keys.RETAILERS, res.data);
      return res.data;
    });
};

/**
 * @param {Array} location - Location properties from back-end response
 * @param {String} type - Marker type: 'kiosk', 'retailer', 'office'
 * @param {Function} clickHandler
 */
const createMarker = (location, type, clickHandler) => {
  let marker = {
    label: '',
    icon: {
      url: '',
      scaledSize: {
        width: 40,
        height: 40,
      },
    },
    position: {
      lat: '',
      lng: '',
    },
    // info object for pop-up window
    info: {
      location: '',
      description: '',
    },
  };

  location.forEach((obj) => {
    switch (obj.name) {
      // case 'Location'
      //   marker.label = obj.value;
      case 'Latitude':
        marker.position.lat = Number(obj.value);
        break;
      case 'Longitude':
        marker.position.lng = Number(obj.value);
        break;
      case 'Location':
        marker.info.location = obj.value;
        break;
      case 'Description':
        marker.info.description = obj.value;
        break;
      default:
    }
  });

  switch (type) {
    case 'office':
      marker.icon.url = officeIcon;
      // marker.label = 'Office';
      marker.info.location = 'Head Office';
      marker.info.description = 'Head Office Location';
      break;
    case 'retailer':
      marker.icon.url = terminalIcon;
      break;
    case 'kiosk':
    default:
      marker.icon.url = kioskIcon;
  }

  marker = new google.maps.Marker(marker);

  // TODO: check performance(prob use delegation)
  marker.addListener('click', function markerClick(event) {
    clickHandler(this.info, event.ya && event.ya.target);
  });

  return marker;
};

const getUserLocation = () => new Promise((resolve, reject) => {
  const success = (pos) => {
    resolve({
      lat: pos.coords.latitude,
      lng: pos.coords.longitude,
    });
  };

  const failed = () => {
    reject();
  };

  if (navigator.geolocation) {
    navigator.geolocation.getCurrentPosition(success, failed);
  } else {
    reject(new Error('no support for geolocation'));
  }
});

/**
 * @param {HTMLElement} mapNode
 * @param {Object} officeLocation
 * @param {Function} handleMarkerClick
 */
export const initMap = (mapNode, officeLocation, handleMarkerClick) => {
  const center = {
    lat: Number(officeLocation.lat),
    lng: Number(officeLocation.lng),
  };
  const zoom = 4;


  const map = new google.maps.Map(
    mapNode, {
      zoom,
      center,
      disableDefaultUI: true,
    },
  );

  getUserLocation()
    .then((pos) => {
      map.setCenter(pos);
      map.setZoom(8);
    })
    .catch(() => null);

  Promise.all([getKiosks(), getRetailers()])
    .then((res) => {
      const markers = [];

      const kiosks = res[0];
      const retailers = res[1];

      kiosks.forEach(kiosk => markers.push(createMarker(kiosk, 'kiosk', handleMarkerClick)));
      retailers.forEach(retailer => markers.push(createMarker(retailer, 'retailer', handleMarkerClick)));

      markers.push(createMarker([
        { name: 'Latitude', value: officeLocation.lat },
        { name: 'Longitude', value: officeLocation.lng },
      ], 'office', handleMarkerClick));

      // eslint-disable-next-line no-unused-vars
      const cluster = new MarkerClusterer(map, markers, { imagePath: 'public/images/markers/cluster/' });
    });
};
