import { createSelector } from 'reselect';
import { currentPageSelector, pageButtonsSelector } from 'src/services/pages/selectors';


export const optionsSelector = state => state.options;

export const linksSelector = createSelector(
  [optionsSelector],
  (options) => {
    const linksObject = options.find(e => e.name === 'links');

    if (linksObject) return linksObject.items;
    return [];
  },
);

export const headerLinksSelector = createSelector(
  [linksSelector, currentPageSelector],
  (links, currentPage) => [...links]
    .filter(link => link.enabled)
    // Don't show the link for current page(except contacts)
    .filter(link => (link.target === 'Contacts') || (link.target !== currentPage))
    .sort((prev, next) => (prev.priority < next.priority ? -1 : 1)),
);

export const buttonsAndLinksSelector = createSelector(
  [pageButtonsSelector, headerLinksSelector],
  (pageButtons, links) => {
    // Some buttons on a page may repeat some links
    const filteredButtons = pageButtons
      .filter(btn => links.findIndex(link => link.name === btn.name) === -1);

    return [...filteredButtons, ...links]
      .sort((prev, next) => (prev.priority < next.priority ? -1 : 1));
  },
);

export const socLinksSelector = createSelector(
  [optionsSelector],
  options => options.filter(option => option.name === 'facebook_url' || option.name === 'twitter_url'),
);

export const supportNumberSelector = createSelector(
  [optionsSelector],
  (options) => {
    const obj = options.find(e => e.name === 'support_number');

    if (obj) return obj.value;
    return '';
  },
);

