export const locationLinkSelector = createSelector(
  [linksSelector],
  links => links.find(link => link.name === 'Location') || {},
);

export const officeAddressSelector = createSelector(
  [optionsSelector],
  (options) => {
    const obj = options.find(option => option.name === 'office_address');

    if (obj) return obj.value;
    return '';
  },
);

export const officeLocationSelector = createSelector(
  [optionsSelector],
  (options) => {
    const obj = options.find(option => option.name === 'office_geolocation');

    if (obj) {
      const values = obj.value.split(',');

      return {
        lat: Number(values[0]),
        lng: Number(values[1]),
      };
    }
    return {};
  },
);


