import React from 'react';
import PropTypes from 'prop-types';
import {
  PageBody,
  PageBodyContent,
  PageBodyForm,
  PageContentWrap,
  PageHeader,
  PageHeaderBack,
  PageHeaderTitle,
  PageLoader,
  ResponseClose,
  ResponseContent,
  ResponseField,
  ResponseFieldLabel,
  ResponseFieldValue,
  ResponseList, ResponseLists,
  ResponseWrap,
} from 'src/components/PageContent/styled';
import { withRouter } from 'react-router-dom';
import { parseMarkdownToHtml } from 'src/utils/utils';
import Buttons from 'src/components/Buttons';
import Form from 'src/components/Form';
import Loader from 'src/components/Loader';
import Map from 'src/components/Map';
import { sendForm } from 'src/services/network';
import { connect } from 'react-redux';


class PageContent extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isRequesting: false,
      response: null,
    };
  }

  submitForm = (event) => {
    const { pageName } = this.props;

    event.preventDefault();
    this.setState(() => ({ isRequesting: true }));

    switch (pageName) {
      case 'CheckPayment':
      case 'MyLastYakaToken':
        this.props.sendForm()
          .then(data => this.setState(() => ({ response: data, isRequesting: false })))
          .catch(() => this.setState(() => ({ isRequesting: false })));
        break;
      default:
        console.warn('Unknown page form');
        this.setState(() => ({ isRequesting: false }));
    }

    document.documentElement.scrollTop = 0;
    event.target.reset();
  };

  clearResponseData = () => {
    this.setState(() => ({ response: null }));
  };

  goBack = () => {
    this.props.history.goBack();
  };

  renderPageBodyContent = html => (
    <PageBodyContent
      dangerouslySetInnerHTML={{ __html: html }}
    />
  );

  renderFormResponse = () => (
    <ResponseWrap>
      <ResponseClose onClick={this.clearResponseData} />
      <ResponseLists>
        {this.state.response.map((resItem, i) => (
        // eslint-disable-next-line react/no-array-index-key
          <ResponseList key={i}>
            <ResponseContent>
              {resItem.map(field => (
                <ResponseField key={`${field.name}_${field.value}`}>
                  <ResponseFieldLabel>
                    {field.label}
                    <span>:</span>
                  </ResponseFieldLabel>
                  <ResponseFieldValue value={field.value}>{field.value}</ResponseFieldValue>
                </ResponseField>
              ))}
            </ResponseContent>
          </ResponseList>
        ))}
      </ResponseLists>
    </ResponseWrap>
  );

  renderPageBodyForm = form => (
    <PageBodyForm>
      <Form
        id="page-form"
        form={form}
        onSubmit={this.submitForm}
      />
    </PageBodyForm>
  );

  renderPageLoader = () => (
    <PageLoader>
      <Loader />
    </PageLoader>
  );

  renderPageBody = () => {
    const { pageConfig } = this.props;
    const { response, isRequesting } = this.state;
    const { content, form } = pageConfig;

    return (
      <PageBody>
        {isRequesting && this.renderPageLoader()}
        {(response && !!response.length) && this.renderFormResponse()}
        {content && this.renderPageBodyContent(parseMarkdownToHtml(content))}
        {form && this.renderPageBodyForm(form)}
      </PageBody>
    );
  };

  render() {
    const { pageConfig, pageName } = this.props;
    const {
      title, content, form, buttons, map,
    } = pageConfig;

    return (
      <PageContentWrap pageName={pageName}>

        <PageHeader pageName={pageName} id="page-title">
          <PageHeaderBack
            onClick={this.goBack}
            direction="left"
          />
          <PageHeaderTitle>{title}</PageHeaderTitle>
        </PageHeader>

        {!!buttons.length && <Buttons className="hidden-desktop-up" />}
        {(content || form) && this.renderPageBody()}
        {map && <Map />}
      </PageContentWrap>
    );
  }
}


PageContent.propTypes = {
  pageConfig: PropTypes.shape({
    title: PropTypes.string.isRequired,
    content: PropTypes.string,
    form: PropTypes.object,
    buttons: PropTypes.array,
    map: PropTypes.bool,
  }),
  history: PropTypes.shape({
    goBack: PropTypes.func.isRequired,
  }).isRequired,
  pageName: PropTypes.string.isRequired,
  sendForm: PropTypes.func.isRequired,
};

PageContent.defaultProps = {
  pageConfig: {
    buttons: [],
  },
};

const mapDispatchToProps = dispatch => ({
  sendForm: () => dispatch(sendForm()),
});

export default withRouter(connect(null, mapDispatchToProps)(PageContent));
