import styled from 'styled-components';
import { colors } from 'src/utils/constants';
import { PageContentWrap } from 'src/components/PageContent/styled';

export const Button = styled.a`
  display:flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content:center;
  border: 1px solid ${colors.border};
  background: #ffffff;
  padding: 10px;
  width: 50%;
  height: calc((100vw - 60px) / 2);
  max-width: 170px;
  max-height: 170px;
  cursor:pointer;
  transition: color .2s;
  border-top: none;
  
  &:nth-child(1),
  &:nth-child(2) {
    border-top: 1px solid ${colors.border};
  }
  
  &:nth-child(2n) {
    margin-left: -1px;
  }

  &:hover {
    color: ${colors.accent};
  }
  
  // Content with buttons
  ${PageContentWrap} & {
    width: calc(100 / 3 * 1%); 
    max-height: 140px;
    
    &:nth-child(3) {
      margin-left: -1px;
      border-top: 1px solid ${colors.border};
    }
    
    &:nth-child(2n) {
      margin-left: 0;
    }
    
    &:nth-child(2) {
      margin-left: -1px;
    }
  }
  
  @media(min-width: 768px) {
    width: calc(100 / 3 * 1%);
    height: calc(60vw * 0.33);
    max-width: 220px;
    max-height: 220px;
    
    &:nth-child(2n) {
      margin-left: 0;
    }
    
    &:nth-child(3n),
    &:nth-child(3n + 2){
      margin-left: -1px;
    }
    
    &:nth-child(3) {
      border-top: 1px solid ${colors.border};
    }
  }
`;

export const ButtonsWrap = styled.div`
  display:flex;
  flex-wrap:wrap;
  width: calc(100vw - 60px);
  max-width: 340px;
  margin: 0 auto;
  
  ${PageContentWrap} & {
    margin: 20px auto;
    width: calc(100% - 40px);
    justify-content:center;
  }
  
  ${PageContentWrap} ~ & {
      
    @media(min-width: 1024px) {
    flex-flow: column nowrap;
    width: 160px;
    flex-shrink: 0;
    
      ${Button} {
        width: 100%;
        height: 160px;
        border-bottom: 1px solid ${colors.border};
        border-right: 1px solid ${colors.border} !important;
        border-top: none;
        margin-left: -1px !important;
        
        &:first-child {
          border-top: 1px solid ${colors.border};
        }
      }
    }
  }
  
  @media(min-width: 768px) {
    width: calc(60vw);
    max-width: 660px;
    margin-right: 0;
    margin-left: 0;
  }
  
  @media(min-width: 1024px) {
    margin-bottom: auto;
  }
`;


export const ButtonIcon = styled.div`
  background-repeat:no-repeat;
  background-position: center;
  background-size:contain;
  background-image: url(${props => props.icon});
  width: 50px;height: 50px;
  margin-bottom: 12px;
  transition: all .2s;
  
  ${Button}:hover & {
    transform: scale(1.1);
  }
  
  @media(min-width: 1024px) {
    width: 80px;
    height: 80px;
    margin-bottom: 20px;
  }
`;

export const ButtonTitle = styled.span`
  text-align:center;
  
  @media(min-width: 1024px) {
    font-size: 18px;
  }
`;
